/* ======================================================
    <!-- Show More Less -->
/* ====================================================== */
.show-more-less__wrapper {
  position: relative;
  height: 8.5rem;
  overflow: hidden;
  /* Actived status */
}
.show-more-less__wrapper .show-more-less__mask {
  display: block;
  content: "...";
  text-align: right;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2em;
  overflow: hidden;
}
.show-more-less__wrapper.active .show-more-less__mask {
  display: none;
}
